.sidebar--show {
  transform: translateX(0%);
  flex: 1;
  border-right: 0.5px solid rgb(230, 227, 227);
  min-height: 100vh;
  .top {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;

    .logo {
      font-size: 20px;
      font-weight: bold;
      color: #888;
    }
  }

  hr {
    height: 0;
    border: 0.5px solid rgb(230, 227, 227);
  }

  .center {
    padding-left: 10px;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        margin-top: 6px;
        margin-bottom: 6px;
        display: flex;
        align-items: center;
        padding: 5px;
        cursor: pointer;

        &:hover {
          background-color: #ece8ff;
        }

        .icon {
          font-size: 28px;
          color: #888;
        }

        span {
          font-size: 18px;
          font-weight: 600;
          color: #888;
          margin-left: 10px;
        }
      }
    }
  }

  .profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3px;

    .avatar {
      width: 150px;
      padding: 15px 15px 15px 0;
      height: 150px;
      border-radius: 50%;
    }

    span {
      font-size: 18px;
      font-weight: 600;
      color: #888;
      padding-top: 9px;
      padding-left: 4px;
    }
  }

  .bottom {
    display: flex;
    align-items: center;
    margin: 10px;

    .colorOption {
      width: 20px;
      height: 20px;
      border-radius: 5px;
      border: 1px solid #7451f8;
      cursor: pointer;
      margin: 5px;

      &:nth-child(1) {
        background-color: #fff;
      }
      &:nth-child(2) {
        background-color: pink;
      }
    }
  }
}

.sidebar--hide {
  transform: translateX(-100%);
  display: none;
  .avatar {
    display: none;
  }
}

.hamburger-icon {
  display: none;
  // margin-top: 10px;
  // margin-left: 5px;
  // font-size: 1.5rem;
  // cursor: pointer;
  // z-index: 2;
}

@media screen and (max-width: 480px) {
  .sidebar--show{
    position: fixed;
    height: 100%;
    background-color: #fff;
    padding-left: 25px;
    padding-right: 25px;
    z-index: 3;
  }
  .hamburger-icon {
    display: inline;
    position: relative;
    z-index: 4;
    margin-top: 8px;
    margin-left: 5px;
    font-size: 1.8rem;
    cursor: pointer;
  }

}
