.new {
  width: 100%;
  display: flex;

  .newContainer {
    flex: 6;
    padding-top: 4rem;

    .top,
    .bottom {
      -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
      box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
      padding: 20px;
      margin: 20px;
      display: flex;

      h1 {
        color: lightgray;
        font-size: 20px;
      }

      form {
        display: flex;
        flex: 2;
        flex-wrap: wrap;
        gap: 20px;
        justify-content: space-around;

        .left {
          flex: 1;
          text-align: center;

          img {
            width: 150px;
            height: 150px;
            border-radius: 50%;
            object-fit: cover;
          }
        }

        .right {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          // flex: 2;
          // gap: 20px;

          .col {
            width: calc(50% - 10px); // 10px is for the gap between columns
            margin-bottom: 20px;
            // display: flex;
            // padding: 20px;
            // flex: 2;
            // width: 50%;
            // box-sizing: border-box;
            // flex-wrap: wrap;
            // gap: 20px;
            // justify-content: space-around;

            // form {
            //   display: flex;
            //   flex: 2;
            //   flex-wrap: wrap;
            //   gap: 20px;
            //   justify-content: space-around;

            .formInput {
              width: 80%;

              label {
                display: flex;
                align-items: center;
                gap: 10px;

                .icon {
                  cursor: pointer;
                }
              }

              //   input {
              //     width: 100%;
              //     padding: 5px;
              //     border: none;
              //     // border-bottom: 1px solid gray;
              //   }
            }

            button {
              width: 150px;
              padding: 10px;
              border: none;
              background-color: teal;
              color: white;
              font-weight: bold;
              cursor: pointer;
              margin-top: 10px;
            }
          }
        }
      }
    }
  }
}
