.home {
  display: flex;

  .homeContainer {
    flex: 6;

    .widgets {
      display: flex;
      padding: 20px;
      gap: 20px;
    }

    .charts {
      display: flex;
      padding: 5px 20px;
    }

    .listContainer {
      -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
      box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
      padding: 20px;
      margin: 20px;

      .listTitle {
        font-weight: 500;
        color: gray;
        margin-bottom: 15px;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .home {
    .homeContainer {
      width: 98%;
      .widgets {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(200px, 4fr));
        padding: 40px;
        grid-gap: 10px;
      }
      .charts {
        display: flexbox;
        // grid-template-columns: repeat(auto-fit, minmax(300px, 2fr));
        padding: 5px 20px;
      }
      .listContainer {
        padding: 40px;
      }
    }
  }
}
