.formContainer {
  background-color: rgb(229, 108, 108);
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .formWrapper {
    background-color: white;
    border: 1px solid black;
    // width: 40%;
    padding: 20px 60px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;

    .logo {
      color: #888;
      font-weight: bold;
      font-size: 24px;
    }

    .title {
      color: #888;
      font-size: 16px;
    }

    form {
      display: flex;
      flex-direction: column;
      gap: 15px;

      input {
        padding: 15px;
        border: none;
        width: 250px;
        outline: none;
        &::placeholder {
          color: rgb(175, 175, 175);
        }
      }

      button {
        background-color: black;
        color: white;
        padding: 10px;
        margin-bottom: 15px;
        font-weight: bold;
        border: none;
        cursor: pointer;
      }

      label {
        display: flex;
        align-items: center;
        gap: 10px;
        color: rgb(175, 175, 175);
        font-size: 12px;
        cursor: pointer;
      }
    }
  }
}

// Add media queries for mobile devices
@media screen and (max-width: 480px) {
  .formContainer {

    .formWrapper {
      padding: 20px 30px;
    }
  }
}

